import React from 'react';
import { useContext, useRef, useState } from 'react';
import { MsgContext } from '../Context/MsgProvider';
import EmojiProvider from './emojiContainer';
const TypingBox = () => {
    const { setMsgData } = useContext(MsgContext);
    const msgInput = useRef();
    const [inputBoxEmpty, setInputBoxEmpty] = useState(true);
    let pressedKeys = [];

    function sendMsg(e) {
        e.preventDefault();
        setMsgData(
            prv => {
                let date = new Date();
                return [
                    ...prv,
                    {
                        msg: msgInput.current.value,
                        msgType: 'myMsg',
                        time: (date.getHours() % 12) + ':' + (date.getMinutes()), 
                        seen: 'sent',
                        id: Math.random() * 10000
                    }
                ]
            }
        );
        setTimeout(() => {
            msgInput.current.value = '';
            changeSendIcon();
        }, 5);

    }
    function checkKeyDown(e) {
        if (e.keyCode === 16 && !pressedKeys.includes(16)) {
            pressedKeys.push(e.keyCode)
        }
        if (e.key === 'Enter' && !pressedKeys.includes(16)) {
            sendMsg(e)
        }
    }
    function keyUpHandler(e) {
        if (e.keyCode === 16) {
            pressedKeys.splice(pressedKeys.indexOf(16), 1)
        }
    }
    function changeSendIcon() {
        if (msgInput.current.value !== '') {
            setInputBoxEmpty(false);
        } else {
            setInputBoxEmpty(true)
        }
    }

    return (
        <form className="typingBox" onSubmit={sendMsg}>
            <div className="emojies"><i className="fa fa-smile-o" aria-hidden="true"></i> </div>
            <EmojiProvider inputBox={msgInput} />
            <div className="attachment"><i className="fa fa-paperclip" aria-hidden="true"></i></div>
            <textarea
                type="text" id="msgInput"
                onKeyDown={checkKeyDown}
                onKeyUp={keyUpHandler}
                onChange={changeSendIcon}
                placeholder="Type a message"
                autoComplete="off"
                ref={msgInput} rows="1">
            </textarea>
            {inputBoxEmpty ? <div className="voiceType"><span className="material-icons">mic</span></div> : <button className='sendBtn'><span className="material-icons">send</span></button>}
        </form>
    );
}

export default TypingBox;
