import React from 'react';

const WelcomePage = ({setPage}) => {

    function clickHandler(){
        setPage('SignUpPage')
    }

    return (
        <div id="wContainer">
            <h1>Welcome to WhatsApp</h1>
            <figure className='logo'>
                <img src="images/whatsapp_logo.png" alt="whatsapp logo" />
            </figure>
            <p className='tmc-para'>Read our <a href="#dfs">Privacy Policy.</a> Tap "Agree and Continue" to Accept the <a href="#dfsf">Terms of Service.</a></p>
            <button id="start" onClick={clickHandler}>Agree And Continue</button>

            <div className="fromMe">
                <p>Made By</p>
                <p className='myName'>Amit Arya</p>
            </div>
        </div>
    );
}

export default WelcomePage;