import { useRef, useState } from 'react';
import React from 'react';
import '../../css/signUp.css';

const SignUpPage = ({ setPage }) => {
    let [showPass, setShowPass] = useState(false);
    let passInp = useRef();
    let passLabel = useRef();
    let nameInp = useRef();

    function turnEye() {
        if (showPass) {
            passInp.current.type = 'text';
            passLabel.current.innerHTML = '<i class="fa fa-eye" aria-hidden="true"></i>';
            setShowPass(false)
        } else {
            passInp.current.type = 'password'
            passLabel.current.innerHTML = '<i class="fa fa-eye-slash" aria-hidden="true"></i>';
            setShowPass(true)
        }
    }

    function nameInpChange(e){
        nameInp.current.value = e.target.value;
    }
    function passInpChange(e){
        passInp.current.value = e.target.value;
    }

    // function goToChatPage() {
    //     setPage('ChatPage')
    // }

    function logIn(e) {
        e.preventDefault();
        console.log('log in');
        fetch('http://localhost:4000/user/SignUp', {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({name: e.target.name.value, pass: e.target.pass.value})
        }).then(res => res.json())
            .then(data => { console.log(data) })
            .catch(err => console.log("ERR", err))
        console.log(e.target.pass.value)


        // axios.post('http://localhost:4000/user/log_in', { name: e.target.name.value, pass: e.target.pass.value })
        // .then(res => console.log(res))
    }

    function loginbtnHandler(){
        setPage('LoginPage')
    }

    return (
        <div className="signUp-page">
            <header>
                <figure>
                    <img src="images/whatsapp_logo.png" alt="" />
                </figure>
                <p>WhatsApp</p>
            </header>
            <main>
                <h3>Create a New Account</h3>
                <form onSubmit={logIn}>
                    <label>Phone</label>
                    <div className="inputContainer">
                        <select name="" id="countryCode">
                            <option value="+91">+91</option>
                        </select>
                        <input type="number" />
                    </div>

                    <label>Email or User Name</label>
                    <div className="inputContainer" >
                        <input onChange={nameInpChange} type="text" name="name" ref={nameInp} />
                    </div>

                    <label>Password</label>
                    <div className="inputContainer" >
                        <input onChange={passInpChange} type="password" name="pass" ref={passInp} /><label onClick={turnEye} ref={passLabel}> <i className="fa fa-eye-slash" aria-hidden="true"></i></label>
                    </div>

                    <button id="signUpBtn" >Sign Up</button>
                    <p>or</p>
                    <div className="loginSocialContainer">
                        <button className='googleBtn'>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <g transform="matrix(1, 0, 0, 1, 27.009001, -39.238998)">
                                    <path fill="#4285F4" d="M -3.264 51.509 C -3.264 50.719 -3.334 49.969 -3.454 49.239 L -14.754 49.239 L -14.754 53.749 L -8.284 53.749 C -8.574 55.229 -9.424 56.479 -10.684 57.329 L -10.684 60.329 L -6.824 60.329 C -4.564 58.239 -3.264 55.159 -3.264 51.509 Z" />
                                    <path fill="#34A853" d="M -14.754 63.239 C -11.514 63.239 -8.804 62.159 -6.824 60.329 L -10.684 57.329 C -11.764 58.049 -13.134 58.489 -14.754 58.489 C -17.884 58.489 -20.534 56.379 -21.484 53.529 L -25.464 53.529 L -25.464 56.619 C -23.494 60.539 -19.444 63.239 -14.754 63.239 Z" />
                                    <path fill="#FBBC05" d="M -21.484 53.529 C -21.734 52.809 -21.864 52.039 -21.864 51.239 C -21.864 50.439 -21.724 49.669 -21.484 48.949 L -21.484 45.859 L -25.464 45.859 C -26.284 47.479 -26.754 49.299 -26.754 51.239 C -26.754 53.179 -26.284 54.999 -25.464 56.619 L -21.484 53.529 Z" />
                                    <path fill="#EA4335" d="M -14.754 43.989 C -12.984 43.989 -11.404 44.599 -10.154 45.789 L -6.734 42.369 C -8.804 40.429 -11.514 39.239 -14.754 39.239 C -19.444 39.239 -23.494 41.939 -25.464 45.859 L -21.484 48.949 C -20.534 46.099 -17.884 43.989 -14.754 43.989 Z" />
                                </g>
                            </svg>
                            Continue with Google</button>
                    </div>
                    <p>New Here? <a href="#j" onClick={loginbtnHandler} className='signUpAndlogin'>Login</a></p>
                </form>
            </main>
        </div>
    );
}

export default SignUpPage;
