import React from 'react';

const Thread = ({ chatData }) => {
    // console.log(data.name)
    return (
        <div className="thread">
            <div className="dpAndNameContainer">
                <figure className="chatDp">
                    {
                        chatData.dp ? <img src={chatData.dp} alt="Profile"></img> : <i className="fa fa-user-circle" aria-hidden="true"></i>
                    }
                </figure>

                <div className="nameContainer">
                    <h3 className="name">{chatData.name} </h3>
                    <p className="msgStatus">{chatData.typing ? <span className="typing">Typing..</span> : chatData.lastMsg}</p>
                </div>
            </div>
            <div className="timeAndOthers">
                <p className="time">11:40</p>
                {chatData.unread ? <p className="msgCount">{chatData.unread}</p> : ''}
            </div>
        </div>
    );
}

export default Thread;
