import React, { useContext } from 'react';
import { ThreadContext } from '../Context/ThreadProvider';
import Thread from './Thread';

const ChatThreads = () => {
    const value = useContext(ThreadContext);

    return (
        <div className="chatThreadContainer">
            <header>
                <div className="dp"><i className="fa fa-user-circle" aria-hidden="true"></i></div>
                <div className="cNoContainer">
                    <div className="contacts"><span className="material-icons">chat</span></div>
                    <div className="options"><span className="material-icons">more_vert</span></div>
                </div>
            </header>

            <div className="searchContainer">
                <div className="search"><span className="material-icons">search</span></div>
                <input type="search" name="" id="" placeholder="Search or start a new chat" />
            </div>

            <div className="threadContainer">
                {value.map(elem => <Thread chatData={elem} key={elem.id} />)}

            </div>
        </div>
    );
}

export default ChatThreads;
