import React from 'react';

const ChatContHeader = () => {
    return (
        <header>
            <div className="dp-name-lastseen">
                <figure className="chatDp"><i className="fa fa-user-circle" aria-hidden="true"></i></figure>
                <div className="name-lastseen">
                    <h3>John</h3>
                    <p className="lastSeen">last seen today at 8:05pm</p>

                </div>
            </div>
            <div className="search-chatOption">
                <div className="search">
                    <span className="material-icons"  >
                        search
                    </span>
                </div>
                <div className="chatOption">
                    <span className="material-icons">more_vert</span>
                </div>
            </div>
        </header>
    );
}

export default ChatContHeader;
