import React from 'react';
import { createContext, useState } from 'react';

export const MsgContext = createContext();

const MsgProvider = (props) => {
    console.log('msgprovider ran');

    const [msgData, setMsgData] = useState([
        {
            msg: 'Hey Amit..!',
            time: '11:45pm',
            msgType: 'theirMsg',
            seen: 'sent',
            id: Math.random() * 10000
        },
        {
            msg: 'Hey..',
            time: '11:46pm',
            msgType: 'myMsg',
            seen: 'sent',
            id: Math.random() * 10000
        },
        {
            msg: 'How are you?',
            time: '11:47pm',
            msgType: 'theirMsg',
            seen: 'sent',
            id: Math.random() * 10000
        },
        {
            msg: 'Awsome!!',
            time: '11:48pm',
            msgType: 'myMsg',
            seen: 'seen',
            id: Math.random() * 10000
        }
    ]);
    
    
    return (
        <MsgContext.Provider value={{msgData, setMsgData}}>
            {props.children}
        </MsgContext.Provider>
    );
}

export default MsgProvider;
