import React from 'react';
import { useContext, useRef, useEffect } from 'react';
import MsgComp from './MsgComp';
import { MsgContext } from '../Context/MsgProvider';
// import { ThreadContext } from '../Context/ThreadProvider'

const MsgContainer = () => {
    // const threadData = useContext(ThreadContext);
    const { msgData, setMsgData } = useContext(MsgContext);
    const msgContainer = useRef('msgContainer');
    const scrollBtn = useRef('scrollBtn');

    useEffect(() => {
        canScroll();
    }, [msgData]);

    function canScroll() {
        let containerScrollHeight = msgContainer.current.scrollHeight;
        let containerClientHeight = msgContainer.current.clientHeight;

        if (containerScrollHeight > containerClientHeight) {
            // alert('new msg')
            scrollBtn.current.style.display = 'block';
            
            setTimeout(() => {
                scrollBtn.current.style.transform = 'scale(1)';
            }, 10);
        }
    }

    function scrollDown() {
        console.log('scrolled')
        msgContainer.current.scrollTo(0, msgContainer.current.clientHeight);
        scrollBtn.current.style.transform = 'scale(0)';
        setTimeout(() => {
            scrollBtn.current.style.display = 'none';
        }, 200);
    }
    return (
        <div className="msgContainer" ref={msgContainer}>
            {msgData.map(dt => <MsgComp key={dt.id} data={dt} setMsgData={setMsgData} />)}
            <div className="scrollDownBtn" onClick={scrollDown} ref={scrollBtn}><i className="fa fa-angle-double-down" aria-hidden="true"></i></div>
        </div>
    );
}

export default MsgContainer;
