import React from 'react';
import { useState, useEffect } from 'react';

const MsgComp = ({ data, setMsgData }) => {
    // console.log(data)
    const [seenIcon, setSeenIcon] = useState('');
    const [seenClass, setSeenClass] = useState('');
    useEffect(() => {
        if (data.seen === 'seen' && data.msgType === 'myMsg') {
            setSeenIcon('done_all');
            setSeenClass('seen');
        } else if (data.seen === 'sent' && data.msgType === 'myMsg') {
            setSeenClass('');
            setSeenIcon('done');
        } else if (data.seen === 'recieved' && data.msgType === 'myMsg') {
            setSeenClass('');
            setSeenIcon('done_all');
        }
    }, [data]);

    return (
        <div className={"msgBodyContainer " + data.msgType}>

            <div className={'msgBody '} >
                <pre>
                    <p className="msg">{data.msg}</p>
                </pre>

                <div className="timeAndSeen">
                    <span className="msgTimeAndSeen" >{data.time}<i className={"material-icons seenIcon " + seenClass}>{seenIcon}</i></span>
                </div>
            </div>
        </div>
    );
}

export default MsgComp;
