import './App.css';
import { useState } from 'react';
import WelcomePage from './Components/PageComponents/Welcome_page';
import SignUpPage from './Components/PageComponents/SignUpPage';
import ChatPage from './Components/PageComponents/ChatPage';
import LoginPage from './Components/PageComponents/LoginPage';

function App() {
  let [page, setPage] = useState('ChatPage');

  if (page === 'WelcomePage') {
    return (
      <div className='App'>
        <WelcomePage setPage={setPage} />
      </div>
    );

  } else if (page === 'SignUpPage') {
    return (
      <div className='App'>
        <SignUpPage setPage={setPage}/>
      </div>
    );
  }else if(page === 'LoginPage'){
    return (
      <div className='App'>
        <LoginPage setPage={setPage}/>
      </div>
    );
  } else if (page === 'ChatPage') {
    return (
      <div className='App'>
        <div className="navStripe"></div>
        <ChatPage />
      </div>
    );
  }
  else {
    return <h1>404 page not found</h1>
  }
}

export default App;
